import { getComments } from "@/api/article";
export default {
  data() {
    return {
      comments: [], // 文章列表
    };
  },
  props: {
    article: {
      type: Object,
      required: true,
    },
  },

  components: {},

  computed: {},

  async mounted() {
    const { data } = await getComments(this.article.slug);
    console.log(data);
    this.comments = data.comments;
  },

  methods: {},
};
